var React = require('react');
var ReactDOM = require('react-dom');

var Dispatcher = require('flux').Dispatcher;
var EventEmitter = require('events').EventEmitter;

//  flux :

//Dispatcher /         
var dispatcher = new Dispatcher;

//Actions /   ,    
function emitClick() {
    dispatcher.dispatch({
        type: 'CLICK'
    })
}

//Stores /        -  ,   
class AppStore extends EventEmitter {

    handleActions(action) {
        switch (action.type) {
            case 'CLICK': {
                console.log(this);
                this.emit('showText');
                break;
            }
        }
    }
}

const appStore = new AppStore;
//  handleActions   appStore       this
dispatcher.register(appStore.handleActions.bind(appStore));

// Controller Views /   React-,         
//  

class App extends React.Component {
    constructor() {
        super()
        this.state = {
            showText: false
        }
    }

    clickHandler() {
        emitClick();
    }

    componentDidMount() {

        var self = this;
        appStore.on('showText', function () {

            self.setState({ showText: !self.state.showText });
            console.log('showText action');
        })
    }
    render() {
        return (
                <div>
                      <button onClick={this.clickHandler} className='btn btn-success btn-lg'>Click!</button>
                      <h1>{this.state.showText ? this.props.text : '...'}</h1>
                </div>
        )
    }
}

var container = document.getElementById('example');
ReactDOM.render(<App text="Hello from Flux!" />, container); 


